import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable()
export class SettingsService {

  private kind: Array<string>;
  private basis_type: Array<string>;
  public decomposition: Array<string>;
  public iter_or_diag: Array<string>;
  public alias_option: Array<string>;
  public integration_scheme: Array<string>;
  public LES_switch: Array<string>;
  public apply_strong_realitycond_alltime_switch: Array<string>;
  public apply_weak_realitycond_alltime_switch: Array<string>;
  public low_dimension_switch: Array<string>;
  public two_and_half_dimension: Array<string>;
  public two_dimension: Array<string>;
  public dt_switch: Array<string>;
  public helicity_switch: Array<string>;
  public sincostr_switch: Array<string>;

  constructor(private http: HttpClient) {
  	this.http.get<any>("./assets/programConfig.json").subscribe((conf) => {
		var keys = Object.keys(conf.program);
		for (var i = 0; i < keys.length; i++) {
			var key = keys[i];
			this[key] = conf.program[key];
		}
  	})
  }

  GetKinds() {
  	return this.kind;
  }

  GetBasisTypes() {
  	return this.basis_type;
  }

}



